import { mapActions } from 'vuex'
import { SearchBox, checkBreak } from 'components'

const TOLANG = '@人员最多选择3个'
const PULLUP = 'PullUp'
const PULLDOWN = 'PullDown'

export default {
  props: {
    selectUsers: {
      type: Array,
      default () {
        return []
      }
    },
    HumanId: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      searchQuery: '',
      offset: 100,
      List: [],
      index: 0,
      size: 20,
      pullDownRefreshThreshold: 60,
      afterSaveMsg: '',
      mx_isLoading: false,
      mx_message: '',
      mx_alertShow: false,
      mx_alertTitle: '提示',
      mx_toastShow: false,
      mx_deleyTime: 1000
    }
  },
  computed: {
    options () {
      return {
        pullDownRefresh: {
          threshold: parseInt(this.pullDownRefreshThreshold),
          txt: '刷新成功'
        },
        pullUpLoad: {
          threshold: parseInt(this.pullDownRefreshThreshold),
          txt: '加载成功'
        },
        scrollbar: true
      }
    },
    usersId () {
      let users = [...this.selectUsers]
      let usersId = users.map((list) => {
        return list.Id
      })

      return usersId
    }
  },
  methods: {
    // 加载数据
    load (type, callback) {
      let params = this.organizeParams(type)

      this.GridPageLoadData(params).then((response) => {
        let value = response.data.value
        if (value) {
          this.organizeGetData(type, [...JSON.parse(value)])
        }
        callback && callback()
      }).catch((e) => {
        this.AlertShowEvent(e.message)
      })
    },
    // 上拉加载
    onPullingUp () {
      this.load(PULLUP, () => {
        this.$refs.Scroll.forceUpdate()
      })
    },
    // 下拉刷新
    onPullingDown () {
      this.load(PULLDOWN, () => {
        this.$refs.Scroll.forceUpdate()
      })
    },
    // 传递赋值 执行搜索
    searchChange (query) {
      this.searchQuery = query
      this.load()
    },
    // 组织参数
    organizeParams (type) {
      // 过滤自己 直接sql过滤，不需要前端在过滤
      let where = ' 1=1 and Id<>\'' + this.HumanId + '\''

      if (!this.searchQuery) {
        this.size = 20
        if (type === 'PullDown') {
          this.index = 0
        } else if (type === 'PullUp') {
          this.index++
        } else {
          this.index = 0
        }
      } else {
        where += ' and (Name like \'%' + this.searchQuery + '%\'' + ' or Code like \'%' + this.searchQuery + '%\')'
        this.index = 0
        this.size = 0
      }

      let params = {
        KeyWord: 'Human',
        KeyWordType: 'BO',
        index: this.index,
        size: this.size,
        swhere: where,
        select: '',
        sort: '',
        extparams: ''
      }

      return params
    },
    // 组织获取的数据
    organizeGetData (type, data) {
      if (!this.searchQuery) {
        if (type === 'PullDown') {
          this.List = [...data]
        } else if (type === 'PullUp') {
          if (data.length === 0) {
            this.index--
          }

          this.List = [...this.List, ...data]
        } else {
          this.List = [...data]
        }
      } else if (this.searchQuery || this.searchQuery !== '') {
        this.List = [...data]
      }
    },
    // 选择人员数据
    selectItem (item) {
      let users = [...this.selectUsers]

      if (!this.usersId.includes(item.Id)) {
        users.push(item)
      } else {
        users = [...this.filerUsers(users, item.Id)]
      }

      if (users.length > 3) {
        this.AlertShowEvent(TOLANG)
        return false
      }

      this.$emit('getAtUsers', users)
    },
    // 过滤数据
    filerUsers (users, Id) {
      let arr = []

      users.forEach((item) => {
        if (item.Id !== Id) {
          arr.push(item)
        }
      })

      return arr
    },
    // 显示面板， 加载数据
    show () {
      // 显示面板
      this.offset = 0
      // 强制渲染scroll组件
      this.$refs.Scroll.forceUpdate()
      // 加载数据
      this.load()
    },
    // 隐藏面板 清空数据
    hide () {
      this.offset = 100
      this.List = []
    },
    ...mapActions([
      'GridPageLoadData',
      'AlertShowEvent'
    ])
  },
  components: {
    SearchBox,
    checkBreak
  }
}



